import { ke } from "/src/ext/kumquat_polyfill";

import * as syncManager from "../syncManager";
import * as cacheManager from "/src/ext/cache";

export async function syncHistory(data, sendResponse) {
  syncManager.syncHistory(sendResponse).then();
}

export async function uploadWordlist(data, sendResponse) {
  if (!(await ke.canSync())) {
    sendResponse(undefined);
    return;
  }

  data.wordlist.id = data.id;
  data.wordlist.timestamp = data.wordlist.created;
  data.wordlist.words = [];

  delete data.wordlist.created;
  delete data.wordlist.phrases_count;
  delete data.wordlist.server_id;
  delete data.wordlist.pending_removal;

  await syncManager.uploadWordlists([data.wordlist], function (r) {
    if (r.error) console.log(r.message);
  });
  sendResponse({ success: true });
}

export async function deleteHistoryItems(data, sendResponse) {
  await cacheManager.deleteFewById(data.ids, sendResponse);
}

export async function syncPhrasebook(data, sendResponse) {
  await syncManager.syncPhrasebook(sendResponse);
}

export async function uploadWord(data, sendResponse) {
  if (!(await ke.canSync())) {
    sendResponse(undefined);
    return;
  }

  await syncManager.uploadWords(
    [
      {
        id: data.wl_id,
        server_id: data.wl_server_id,
        last_update: data.word.added,
        words: [
          {
            id: data.id,
            added: data.word.added,
            json: data.word.json,
          },
        ],
      },
    ],
    function (r) {
      if (r.error) console.log(r.message);
    }
  );

  sendResponse({ success: true });
}

export async function deleteWordlist(data, sendResponse) {
  if (!(await ke.canSync())) {
    sendResponse(undefined);
    return;
  }

  let words_ids = [];
  let _words_ids = [];

  data.words.forEach(function (word) {
    words_ids.push(word.server_id);
    _words_ids.push(word.id);
  });

  await syncManager.deleteWordlists(
    [
      {
        r: data.r_wl,
        id: data.wl_server_id,
        _id: data.wl_id,
        words: words_ids,
        _words: _words_ids,
      },
    ],
    function (r) {
      if (r.error) console.log(r.message);
    }
  );
  sendResponse({ success: true });
}
