import { ke } from "/src/ext/kumquat_polyfill";
import * as syncManager from "/src/pages/background/syncManager";
import storageUtils from "/src/ext/util/storageUtil";
import * as UILoading from "/src/ui_components/loading";

export function saveOrUpdate(
  from,
  to,
  input,
  output,
  source,
  callback,
  server_item
) {
  callback = callback || ke.EF;

  ke.idb.exists(
    "it",
    "history",
    ["input", input],
    {
      l_from: from,
      l_to: to,
    },
    function (exists, primaryKey, existing_obj) {
      if (exists) {
        const updated_sources = existing_obj.sources || {};
        updated_sources[source] = Date.now();

        const updated_obj = {
          it_resp: output,
          sources: updated_sources,
        };

        if (server_item) {
          updated_obj.server_id = server_item.server_id;
        }

        ke.idb.update("it", "history", primaryKey, updated_obj, function () {
          callback();
        });
      } else {
        const sources = {};
        sources[source] = Date.now();

        const translation_time = server_item
          ? server_item.timestamp
          : Date.now();

        ke.idb.add(
          "it",
          "history",
          {
            l_from: from,
            l_to: to,
            input: input,
            it_resp: output,
            sources: sources,
            server_id: server_item ? server_item.server_id : null,
            pending_removal: false,
            time: translation_time,
          },
          "translation",
          function (id) {
            callback();

            if (id && !server_item && ke.canSync) {
              // try to upload the item to the server
              syncManager.uploadHistoryItems(
                [
                  {
                    id: id,
                    timestamp: translation_time,
                    json: output,
                  },
                ],
                function () {}
              );
            }
          }
        );
      }
    }
  );
}

export function lookUpInCache(from, to, input, callback) {
  ke.idb.exists(
    "it",
    "history",
    ["input", input],
    {
      pending_removal: false,
      l_from: from,
      l_to: to,
    },
    function (exists, primaryKey, existing_obj) {
      if (!exists) {
        callback(true);
      } else {
        callback(false, existing_obj);
      }
    }
  );
}

export function getIdListOfAll(callback) {
  ke.idb.search(
    "it",
    "history",
    {
      pending_removal: false,
    },
    function (items, idb_error) {
      if (!items && idb_error && ke.IS_FIREFOX) {
        UILoading.showIdbErrorLayout();
        return;
      }

      const ids = [];
      for (let i = 0, len = items.length; i < len; ++i) {
        ids.push(items[i].id);
      }
      callback(ids);
    }
  );
}

export function deleteFewByIdOnClient(ids, callback) {
  chrome.runtime.sendMessage(
    {
      action: ke.processCall("app", "sync", "deleteHistoryItems"),
      ids: ids,
    },
    callback
  );
}

export async function deleteFewById(ids, callback) {
  const items = [];

  if (!(await storageUtils.getVal("account_token"))) {
    ke.idb.del("it", "history", ids, function () {
      callback(true);
    });
  } else {
    ids.forEach(function (id) {
      ke.idb.update(
        "it",
        "history",
        id,
        {
          pending_removal: true,
        },
        function (item, local_id) {
          items.push({
            id: local_id,
            server_id: item.server_id,
          });
        }
      );
    });

    const i = setInterval(async function () {
      if (items.length === ids.length) {
        clearInterval(i);
        callback(true);

        if (await ke.canSync()) {
          syncManager.deleteHistoryItems(items, function (r) {
            if (r.error) console.log(r.message);
          });
        }
      }
    }, 10);
  }
}
