export async function isKeyCombo(data, sendResponse) {
  let is_active = false;
  let k,
    val = { from: "", to: "" };
  
  // additional shortcuts
  const combinations = await storageUtils.getDecodedVal("add_trans_combinations");

  // console.log("isKeyCombo", data.keys_down, combinations);

  for (const key in combinations) {
    // console.log("isKeyCombo", combinations, key, data.keys_down, eventUtils.isDown(key, eventUtils.IN_CODES, data.keys_down));
    if (eventUtils.isDown(key, eventUtils.IN_CODES, data.keys_down)) {
      is_active = true;
      k = key;
      val = combinations[key];
      break;
    }
  }

  if (!is_active) {

    // here we're checking against the main shortcut
    is_active |= eventUtils.isDown(
      await storageUtils.getVal("trans_combination"),
      eventUtils.IN_CODES,
      data.keys_down
    );

    if (is_active || data.dblclick) {
      k = await storageUtils.getVal("trans_combination");
      val.from = await langUtils.getFromLang();
      val.to = await langUtils.getToLang();
    }
  }

  sendResponse({
    is_active: !!is_active,
    combo: k,
    from: val.from,
    to: val.to,
  });
}

