import storageUtils from "/src/ext/util/storageUtil";

export async function sendChargedEventIfNeeded(new_sub_data) {
  let prev_sub_data = await storageUtils.getDecodedVal("sub_data");

  if (
    prev_sub_data &&
    prev_sub_data.status === "trialing" &&
    new_sub_data.status === "active"
  ) {
    await storageUtils.setVal("sub_pro_survey", 0);

    // ke.ext.mate_events.send("charged_money");
  }
}
