/**
 * Creates a Ping instance.
 * @returns {Ping}
 * @constructor
 */
export const Ping = function (opt = {}) {
  this.opt = opt;
  this.timeout = this.opt.timeout || 5000; // Default timeout set to 5000 milliseconds
};

/**
 * Pings source and triggers a callback when completed.
 * @param source Source of the website or server, including protocol and port.
 * @param callback Callback function to trigger when completed. Returns error and ping value.
 * @param timeout Optional number of milliseconds to wait before aborting.
 */
Ping.prototype.ping = function (source, callback) {
  const start = new Date();
  let timeoutHandle;

  const handleResponse = (error, pong) => {
    clearTimeout(timeoutHandle); // Clear the timeout on any response
    callback(error, pong);
  };

  fetch(source + "?" + +new Date(), {
    method: 'HEAD', // Use HEAD to minimize data transfer
    mode: 'no-cors' // Required for cross-origin requests in service workers
  }).then(response => {
    const pong = new Date() - start;
    handleResponse(null, pong);
  }).catch(error => {
    const pong = new Date() - start;
    handleResponse("error", pong);
  });

  // Handle timeout
  timeoutHandle = setTimeout(() => {
    handleResponse("timeout", new Date() - start);
  }, this.timeout);
};