import { ke } from "/src/ext/kumquat_polyfill";
import storageUtils from "../../../ext/util/storageUtil";

const newAudio = {
  load: function (data, sendResponse) {
    fetch(data.link, {
      method: "GET",
      headers: {
        "Content-type": "application/json; charset=utf-8",
      },
    })
      .then(async (res) => {
        if (res.status === 200) {
          // const blob = new Blob([this.response], { type: "audio/mpeg" });
          const blob = await res.blob();
          const reader = new FileReader();

          reader.addEventListener("loadend", function () {
            console.log(reader.result);
            sendResponse(reader.result);
          });

          reader.readAsDataURL(blob);
        } else {
          console.error("Unable to download audio chunk");
        }
      })
      .catch((e) => {
        console.error(e);
      });
  },
};

export default newAudio;
