import { ke } from "/src/ext/kumquat_polyfill";
import * as langConstants from "/src/ext/const/lang";
import storageUtils from "/src/ext/util/storageUtil";

// Almost the same object as `ke.ext.const.lang.list` is, but this one is reversed.
// In other words, language code here appears as a key, not value.

var LANG_LIST_REVERSED = {};

var SUPPORT_TRANSLIT =
  "ar,hy,bn,be,bg,zh-CN,zh-TW,ka,el,gu,hi,ja,kn,kk,km,ko,lo,mk,ml,mr,mn,my,my,me,pa,ru,sr,si,tg,ta,te,th,uk,yi".split(
    ","
  );
var RTL = ["ar", "yi", "ur"];
var HIEROGLYPH = ["zh-TW", "zh-CN"];

var bing_langs = {
  auto: "",
  bs: "bs-Latn",
  sr: "sr-Cyrl",
  "zh-TW": "zh-CHT",
  "zh-CN": "zh-CHS",
};

export function supportsTranslit(lang) {
  return SUPPORT_TRANSLIT.includes(lang);
}

export function isHieroglyphical(lang) {
  return HIEROGLYPH.includes(lang);
}

export function isRtl(lang) {
  return RTL.includes(lang);
}

export function getCurrentUiLang(simplify) {
  return ke.getCurrentLocale(simplify);
}

export async function getFromLang() {
  return await storageUtils.getVal("from_lang");
}

export async function getToLang() {
  return await storageUtils.getVal("to_lang");
}

export async function setFromLang(c) {
  await storageUtils.setVal("from_lang", c);
}

export async function setToLang(c) {
  await storageUtils.setVal("to_lang", c);
}

export async function isFromLang(c) {
  return c === (await getFromLang());
}

export async function isToLang(c) {
  return c === (await getToLang());
}

export function simplifyLangCode(c) {
  return ke.simplifyLC(c);
}

export function getDetectedLang(input, callback) {
  ke.idb.exists(
    "it",
    "history",
    ["input", input],
    {
      l_from: "auto",
    },
    function (exists, primaryKey, existing_obj) {
      if (!exists) {
        callback(null);
        return;
      }
      callback(existing_obj.it_resp[5]);
    }
  );
}

export function getLangKeyOrAuto(lang_name) {
  return langConstants.supportedLangs[lang_name] || "auto";
}

export function getLangNameByKey(k) {
  if (k in LANG_LIST_REVERSED) {
    return LANG_LIST_REVERSED[k];
  }

  for (var key in langConstants.supportedLangs) {
    if (k === langConstants.supportedLangs[key]) {
      LANG_LIST_REVERSED[k] = key;
      return key;
    }
  }

  return null;
}

export function getBingSyncedLang(lang) {
  return bing_langs[lang] != undefined ? bing_langs[lang] : lang;
}
