export async function newTab(data, sendResponse) {
  await chrome.tabs.create({ url: data.url });
  sendResponse({ success: true });
}

export async function sendEmail() {
  const pro_subj = (await ke.isProUser()) ? " Pro User Support" : "";

  chrome.tabs.create(
    {
      url:
        "mailto:support@matetranslate.com?subject=Mate for " +
        ke.browserName +
        " v" +
        ke.getExtVer() +
        pro_subj,
    },
    function (tab) {
      setTimeout(async function () {
        await chrome.tabs.remove(tab.id);
      }, 500);
    }
  );
}

