export function downloadFile(data, sendResponse) {
  chrome.tabs.create(
    {
      url: data.url,
    },
    function (tab) {
      setTimeout(async function () {
        try {
          await chrome.tabs.remove(tab.id);
        } catch (e) {}
      }, 2500); // idk when to close the tab actually
    }
  );
  sendResponse();
}

export function attactContentFile(data, sendResponse, sender) {
  if (data.src.endsWith(".css")) {
    chrome.tabs.insertCSS(
      sender.tab.id,
      {
        file: data.src,
      },
      function () {
        sendResponse();
      }
    );
  } else {
    chrome.tabs.executeScript(
      sender.tab.id,
      {
        file: data.src,
      },
      function () {
        sendResponse();
      }
    );
  }
}

export function chainRequestBackgroundOption(data, sendResponse) {
  storageUtils.chainRequestBackgroundOption(data.calls, sendResponse, true);
}

export function closeLoginPopup(data, sendResponse) {
  chrome.tabs.query({}, async function (tabs) {
    let to_close_id = null;
    const to_send_ids = [];

    tabs.forEach(function (tab) {
      if (!tab.url) {
        return;
      }

      if (tab.url.indexOf("login.html") > -1) {
        to_close_id = tab.id;
      } else if (tab.url.indexOf(chrome.runtime.id) > -1) {
        to_send_ids.push(tab.id);
      }
    });

    if (to_close_id !== null) {
      await chrome.tabs.remove(to_close_id);
    }

    to_send_ids.forEach(function (id) {
      chrome.tabs.sendMessage(id, {
        action: ke.processCall("app", "login", "done"),
      });
    });
    sendResponse({ success: true });
  });
}

export async function revokeToken(data, sendResponse) {
  await fetch(
    `https://sync.matetranslate.com/revoke_token?token=${await storageUtils.getVal(
      "revoke_account_token"
    )}`
  );
  sendResponse({});
  await storageUtils.setVal("revoke_account_token", "");
  await updateUninstallUri();
}

// hard coded it for russian speaking users and others
// can route via server to be able to hot swap different surveys in the future
export async function updateUninstallUri(_, sendResponse) {
  const token = await storageUtils.getVal("account_token");
  let country = await storageUtils.getVal("user_country");

  // console.log(token, country);

  if (country.indexOf("ip address") > -1) {
    country = null;
  }

  let data = {
    d: ke.browserName.toLowerCase(),
    d_id: await storageUtils.getVal("user_id"),
    l: navigator.language,
    id: await storageUtils.getIntValue("install_date"),
    v: ke.getExtVer(),
    c: country,
  };

  data = Object.assign(
    {},
    data,
    await storageUtils.getDecodedVal("all_trans_count")
  );

  if (token) {
    data.t = token;
    delete data.d;
  }

  // double check it's less than 255 characters,
  // because otherwise the whole extension won't load
  const url = "https://sync.matetranslate.com/del_dev?" + ke.serialize(data);
  console.log(url);
  console.log("uninstall url length: %d", url.length);
  const uninstall_uri = url.substring(0, 255);

  chrome.runtime.setUninstallURL(uninstall_uri);
  if (sendResponse) {
    sendResponse({ success: true });
  }
}
