import storageUtils from "/src/ext/util/storageUtil";
import { ke } from "/src/ext/kumquat_polyfill";

let client_id
    , gdpr_consent
    , MEASUREMENT_ID
    , API_SECRET;

(async function () {
  client_id = await storageUtils.getVal("user_id");
  gdpr_consent = await storageUtils.isTrueOption("gdpr_consent");

  MEASUREMENT_ID = ke.getAnalyticsMeasurementId();
  API_SECRET = ke.getAnalyticsApiSecret();

  // console.log(`GA4 client_id: ${client_id}`);
  // console.log(`GA4 gdpr_consent: ${gdpr_consent}`);
})();

const GA_ENDPOINT = 'https://www.google-analytics.com/mp/collect';
const GA_DEBUG_ENDPOINT = 'https://www.google-analytics.com/debug/mp/collect';

// Get via https://developers.google.com/analytics/devguides/collection/protocol/ga4/sending-events?client_type=gtag#recommended_parameters_for_reports

const DEFAULT_ENGAGEMENT_TIME_MSEC = 100;

// Duration of inactivity after which a new session is created
const SESSION_EXPIRATION_IN_MIN = 30;

export class Analytics {
  constructor(debug = false) {
    this.debug = debug;
  }

  // Returns the current session id, or creates a new one if one doesn't exist or
  // the previous one has expired.
  async getOrCreateSessionId() {
    // Use storage.session because it is only in memory
    let { sessionData } = await chrome.storage.session.get('sessionData');
    const currentTimeInMs = Date.now();
    // Check if session exists and is still valid
    if (sessionData && sessionData.timestamp) {
      // Calculate how long ago the session was last updated
      const durationInMin = (currentTimeInMs - sessionData.timestamp) / 60000;
      // Check if last update lays past the session expiration threshold
      if (durationInMin > SESSION_EXPIRATION_IN_MIN) {
        // Clear old session id to start a new session
        sessionData = null;
      } else {
        // Update timestamp to keep session alive
        sessionData.timestamp = currentTimeInMs;
        await chrome.storage.session.set({ sessionData });
      }
    }
    if (!sessionData) {
      // Create and store a new session
      sessionData = {
        session_id: currentTimeInMs.toString(),
        timestamp: currentTimeInMs.toString()
      };
      await chrome.storage.session.set({ sessionData });
    }
    return sessionData.session_id;
  }

  sanitizeName(name) {
    if (!name) return '';
    // Replace invalid characters with underscores and truncate if necessary
    return name.replace(/[^a-zA-Z0-9_]/g, '_').substring(0, 40);
  }

  sanitizeParameters(params) {
    if (!params || typeof params !== 'object') return {};
  
    const sanitizedParams = {};
    for (const key in params) {
      if (params.hasOwnProperty(key)) {
        const sanitizedKey = this.sanitizeName(key);
        sanitizedParams[sanitizedKey] = params[key];
      }
    }
    return sanitizedParams;
  }

  // Fires an event with optional params. Event names must only include letters and underscores.
  async fireEvent(name, params = {}) {
    // Configure session id and engagement time if not present, for more details see:
    // https://developers.google.com/analytics/devguides/collection/protocol/ga4/sending-events?client_type=gtag#recommended_parameters_for_reports
    if (!params.session_id) {
      params.session_id = await this.getOrCreateSessionId();
    }
    if (!params.engagement_time_msec) {
      params.engagement_time_msec = DEFAULT_ENGAGEMENT_TIME_MSEC;
    }

    try {
      const response = await fetch(
        `${
          this.debug ? GA_DEBUG_ENDPOINT : GA_ENDPOINT
        }?measurement_id=${MEASUREMENT_ID}&api_secret=${API_SECRET}`,
        {
          method: 'POST',
          body: JSON.stringify({
            client_id,
            events: [
              {
                name: this.sanitizeName(name),
                params: this.sanitizeParameters(params),
              }
            ],
          })
        }
      );
      if (!this.debug) {
        return;
      }
      // console.log(await response.text());
    } catch (e) {
      console.error('Google Analytics request failed with an exception', e);
    }
  }

  // Fire a page view event.
  async firePageViewEvent(pageTitle, pageLocation, additionalParams = {}) {
    return this.fireEvent('page_view', {
      page_title: pageTitle,
      page_location: pageLocation,
      ...additionalParams
    });
  }

  // Fire an error event.
  async fireErrorEvent(error, additionalParams = {}) {
    // Note: 'error' is a reserved event name and cannot be used
    // see https://developers.google.com/analytics/devguides/collection/protocol/ga4/reference?client_type=gtag#reserved_names
    return this.fireEvent('extension_error', {
      ...error,
      ...additionalParams
    });
  }
}

export default new Analytics();