  parseProcessCall: function (command) {
    if (!command) {
      return {
        lib: "",
        cmd: "",
        exact: "",
      };
    }

    const tmp = command.split(",");
    const f = {
      lib: tmp[0],
      cmd: tmp[1],
      exact: tmp[2],
    };
    return f;
  },

