ke.idb.def_obj_struct("langsite", {
  timestamp: 0,
  country: "",
  locale: "",
  site: "",
  is_pro: false,
  count: 0,
  platform: "",
  uploaded: false,
});

console.log('will try to open idb now:', ke.section)

if (ke.section !== "content") {
  try {
    ke.idb.open(
      "it",
      {
        history: {
          l_from: false,
          l_to: false,
          time: false,
          input: false, // index for input w/ unique == false
          server_id: false, // index for server_id w/ unique == false
        },

        wordlists: {
          from: false,
          to: false,
          last_update: false,
          server_id: false,
        },

        phrases: {
          parent_wordlist_key: false,
          server_id: false,
        },

        langsites: {
          site: false,
        },
      },
      35,
      function () {
        console.log('opened idb callback')
      }
    );
  } catch (e) {
    console.log(e);
  }
}
