// import $ from "jquery";

import { ke } from "/src/ext/kumquat_polyfill";
import { ga } from "/src/lib/ga";

import Analytics from "/src/lib/analytics";

import stringUtils from "/src/ext/string";
import * as langUtils from "/src/ext/util/langUtil";
import storageUtils from "/src/ext/util/storageUtil";
import * as cacheManager from "/src/ext/cache";
import * as apiManager from "/src/ext/api";

export async function get(data, sendResponse) {
  const from = data.from || (await langUtils.getFromLang());
  const to = data.to || (await langUtils.getToLang());

  const fn = sendResponse;

  data.text = stringUtils.escapeHtml(data.text);

  const locales =
    data.identificator === "window"
      ? {
          translation: ke.getLocale("Kernel_Listen"),
        }
      : null;

  Analytics.fireEvent(`translation_${data.identificator}_${data.type}`, {
    country: await storageUtils.getVal("user_country"),
    china: (await storageUtils.getVal("user_country")) === "cn" ||
        navigator.language.indexOf("zh") > -1,
  });
  
  // Analytics
  // if (typeof ga != "undefined")
  //   ga("send", "event", "translation", data.identificator, data.type);
  // if (typeof ga != "undefined")
  //   ga(
  //     "send",
  //     "event",
  //     "translation-by-country",
  //     await storageUtils.getVal("user_country")
  //   );
  // if (
  //   typeof ga != "undefined" &&
  //   ((await storageUtils.getVal("user_country")) === "cn" ||
  //     navigator.language.indexOf("zh") > -1)
  // ) {
  //   ga("send", "event", "translation-country", "china (locale or ip)");
  // }

  //
  // count translations by type

  // if (Date.now() - storageUtils.getIntValue('last_trans_count_upd') >= ke.TIME.ONE_DAY) {
  //     handlers.sendTranslationsCount();
  // }

  // if (data.identificator === 'tooltip') {
  //     storageUtils.setJsonField('translations_count', data.type,
  //         +storageUtils.getJsonField('translations_count', data.type) + 1);
  //     storageUtils.setJsonField('all_trans_count', data.type,
  //         +storageUtils.getJsonField('all_trans_count', data.type) + 1);
  // } else {
  //     storageUtils.setJsonField('translations_count', data.identificator,
  //         +storageUtils.getJsonField('translations_count', data.identificator) + 1);
  //     storageUtils.setJsonField('all_trans_count', data.identificator,
  //         +storageUtils.getJsonField('all_trans_count', data.identificator) + 1);
  // }

  // processEventHandlers.app.opt.updateUninstallUri();

  apiManager.getTextTranslation(from, to, data.text, async function (output) {
    if (output.error) {
      Analytics.fireEvent('translation_error', {
        country: await storageUtils.getVal("user_country"),
        china: (await storageUtils.getVal("user_country")) === "cn" ||
            navigator.language.indexOf("zh") > -1,
      });

      cacheManager.lookUpInCache(
        from,
        to,
        data.text,
        async function (isEmpty, item) {
          if (isEmpty) {
            fn({
              offline: true,
            });
          } else {
            const json = item.it_resp;
            // const parsed = apiManager.parseReceivedTranslation(
            //   json,
            //   false,
            //   data.prefix,
            //   locales,
            //   true
            // );

            // parsed[6] = to;

            fn({
              // code: parsed[1],
              parseArgs: [json, false, data.prefix, locales, true],
              trans_translit: [
                await storageUtils.isTrueOption("show_translit"),
                json[4],
              ],
              trans_ipa: [await storageUtils.isTrueOption("show_ipa"), json[2]],
              isMulti: json[0],
              detected_lang: json[5],
              from: from,
              to: to,
              json: json,
            });
          }
        }
      );
    } else {
      const handleTranslation = async function (output_it_format, reversed) {
        if (!output_it_format[3]) {
          fn({
            no_results: true,
          });
        } else {
          const correction = output.spell && output.spell.spell_res;
          // const parsed = apiManager.parseReceivedTranslation(
          //   output_it_format,
          //   false,
          //   data.prefix,
          //   locales,
          //   true
          // );

          const throwDataBack = async function () {
            const correct_from = from === "auto" ? output_it_format[5] : from;
            const correct_to = to;

            if (typeof ga != "undefined")
              ga(
                "send",
                "event",
                "translation-by-lang-pairs",
                correct_from + " <-> " + correct_to
              );

            // =================================
            // save the character stats here too

            // let stats = await storageUtils.getDecodedVal('char_stats');
            // stats.total += data.text.length;
            // ++stats.translations;
            // if (!stats.chars_per_pair[correct_from + '-' + correct_to]) {
            //     stats.chars_per_pair[correct_from + '-' + correct_to] = 0;
            // }
            // stats.chars_per_pair[correct_from + '-' + correct_to] += data.text.length;
            // storageUtils.encodeAndSet('char_stats', stats);

            // =================================
            // =================================
            // console.log(fn);
            fn({
              // code: parsed[1],
              parseArgs: [output_it_format, false, data.prefix, locales, true],
              isMulti: output_it_format[0],
              trans_translit: [
                await storageUtils.isTrueOption("show_translit"),
                output_it_format[4],
              ],
              trans_ipa: [
                await storageUtils.isTrueOption("show_ipa"),
                output_it_format[2],
              ],
              detected_lang: output_it_format[5],
              from: correct_from,
              from_unmodified: from,
              to: correct_to,
              reversed: reversed,
              correction: correction,
              json: output_it_format,
            });
          };

          if (
            !data.dontSaveInCache &&
            (await storageUtils.isTrueOption("history"))
          ) {
            // not var `from` to save auto detected langs

            if (reversed) {
              output_it_format[5] = from;
              output_it_format[6] = to;
            }

            cacheManager.saveOrUpdate(
              from,
              to,
              data.text,
              output_it_format,
              data.source,
              throwDataBack
            );
          } else {
            await throwDataBack();
          }
        }
      };

      const preHandleTranslation = async function (output_it_format, reversed) {
        const ipa_lang = reversed ? from : to;

        if (
          ((await storageUtils.isTrueOption("show_ipa")) ||
            (await storageUtils.isTrueOption("show_articles"))) &&
          await ke.isProUser() &&
          output_it_format[3].length <= apiManager.MAX_IPA_LEN
        ) {
          const info_fetcher_req = [];

          info_fetcher_req.push({
            word: output_it_format[1],
            language: output_it_format[5],
          });

          info_fetcher_req.push({
            word: output_it_format[3],
            language: output_it_format[6],
          });

          if (output_it_format[7][1].length > 0) {
            output_it_format[7][1].forEach((item) => {
              info_fetcher_req.push({
                word: item[0],
                language: output_it_format[6],
              });
            });
          }

          await apiManager.getWordsInfo(info_fetcher_req, function (r) {
            if (Array.isArray(r.words_info)) {
              r.words_info.forEach((item) => {
                if (item.word === output_it_format[1]) {
                  // save stuff for the original

                  if (apiManager.IPA_LANGS.indexOf(output_it_format[5]) > -1) {
                    output_it_format[10] = item.ipa || "";
                  }

                  output_it_format[8] = (item.info && item.info.gender) || "";
                } else if (item.word === output_it_format[3]) {
                  // save stuff for the translation

                  if (apiManager.IPA_LANGS.indexOf(output_it_format[6]) > -1) {
                    output_it_format[2] = item.ipa || "";
                  }

                  output_it_format[9] = (item.info && item.info.gender) || "";
                } else {
                  // save stuff for synonyms

                  if (output_it_format[7][1].length > 0) {
                    for (let j = 0; j < output_it_format[7][1].length; ++j) {
                      if (output_it_format[7][1][j][0] === item.word) {
                        if (
                          !output_it_format[7][1][j][2] &&
                          item.info &&
                          item.info.gender
                        ) {
                          output_it_format[7][1][j][2] = item.info.gender;
                        }
                      }
                    }
                  }
                }
              });
            }

            handleTranslation(output_it_format, reversed);
          });
        } else {
          await handleTranslation(output_it_format, reversed);
        }
      };

      const output_it_format = apiManager.getInternalJSONFormat(
        output,
        data.text
      );
      output_it_format[6] = to;

      if (output_it_format[5] === to && from !== to && from !== "auto") {
        //
        // reverse translation
        //
        apiManager.getTextTranslation(to, from, data.text, function (output) {
          if (output.error) {
            fn({
              offline: true,
            });
          } else {
            const output_it_format = apiManager.getInternalJSONFormat(
              output,
              data.text
            );
            output_it_format[6] = to;

            preHandleTranslation(output_it_format, true);
          }
        });
      } else {
        preHandleTranslation(output_it_format, false);
      }
    }
  });
}

export async function resetMonthlyCounter() {
  if (
    Date.now() - (await storageUtils.getIntValue("last_monthly_reset")) >=
    ke.TIME.ONE_MONTH
  ) {
    await storageUtils.setIntValue("monthly_trans_count", 0);
    await storageUtils.setIntValue("last_monthly_reset", Date.now());
  }
}

