/* Kumquat Extension - Tpl
 *
 **/

const parseHtml = function (h) {
  return h.replace(/&lt;/gi, "<").replace(/&gt;/gi, ">");
};

export function compile(str, data) {
  if (!str) {
    return "";
  }
  data = data || {};
  str = parseHtml(str);

  for (const key in data) {
    str = str
      .replace(new RegExp("<%=" + key + "%>", "g"), data[key])
      .replace(new RegExp("<%= " + key + " %>", "g"), data[key])
      .replace(new RegExp("<-= " + key + " %>", "g"), data[key])
      .replace(new RegExp("<-=" + key + "%>", "g"), data[key]);
  }

  return str;
}
