const MAP = {
  enter: 13,
  shift: 16,
  ctrl: 17,
  alt: 18,
  esc: 27,
  space: 32,
  cmd: [91, 93],
  arrowup: 38,
  arrowdown: 40,
  tilde: 192,
  f1: 112,
  f2: 113,
  f3: 114,
  f4: 115,
  f5: 116,
  f6: 117,
  f7: 118,
  f8: 119,
  f9: 120,
  f10: 121,
  f11: 122,
  f12: 123,
};

const REG_KEYS = {
  "=": 187,
  "-": 189,
  0: 48,
  1: 49,
  2: 50,
  3: 51,
  4: 52,
  5: 53,
  6: 54,
  7: 55,
  8: 56,
  9: 57,
};

const start = 65;
for (let i = start; i <= start + 25; ++i) {
  MAP[String.fromCharCode(i + 32)] = i;
  REG_KEYS[String.fromCharCode(i + 32)] = i;
}

const MOD_KEYS = {
  alt: true,
  ctrl: true,
  shift: true,
  cmd: true,
  f1: true,
  f2: true,
  f3: true,
  f4: true,
  f5: true,
  f6: true,
  f7: true,
  f8: true,
  f9: true,
  f10: true,
  f11: true,
  f12: true,
  win: true,
};

const isModificationKey = function (key) {
  return key in MOD_KEYS;
};

const isRegularKey = function (key) {
  return key in REG_KEYS;
};

export function isKeyCodeLetter(key_code) {
  key_code = +key_code;
  return key_code >= start && key_code <= start + 25;
}

export function isControlKey(key_code) {
  key_code = +key_code;

  for (const key in MAP) {
    const is_arr = Array.isArray(MAP[key]);
    if (
      !isKeyCodeLetter(key_code) &&
      (is_arr ? MAP[key].includes(key_code) : MAP[key] === key_code)
    ) {
      return true;
    }
  }
  return false;
}

export function isValidKeyCode(key_code) {
  key_code = +key_code;
  return isKeyCodeLetter(key_code) || key_code in SPECIAL_KEYS;
}

// for mac cmd
export function checkMultipleKeyCodes(key_code) {
  return key_code in { 91: 0, 93: 0 } ? "91-93" : key_code;
}

export function getNicePresentationForKeyCode(key_code) {
  if (isKeyCodeLetter(+key_code)) {
    return String.fromCharCode(+key_code);
  }

  let found = null;
  for (const key in SPECIAL_KEYS) {
    if (
      typeof key === "string" &&
      key.indexOf(key_code) > -1 &&
      key.indexOf("-") > -1
    ) {
      found = SPECIAL_KEYS[key];
    }
  }

  return found || SPECIAL_KEYS[key_code];
}

export function filter(combo) {
  combo = combo.toLowerCase();

  const c_arr = combo.split("+");
  const n_arr = [];
  let reg_key = null;
  let has_mod_key = false;

  for (let i = 0, len = c_arr.length; i < len; ++i) {
    if (isModificationKey(c_arr[i])) {
      has_mod_key = true;
      n_arr.push(c_arr[i]);
    } else if (isRegularKey(c_arr[i]) && !reg_key) {
      reg_key = c_arr[i];
    }
  }

  if (reg_key) {
    n_arr.push(reg_key);
  }

  return has_mod_key && reg_key ? n_arr.join("+") : "";
}

export function is(combo, e) {
  const multi = combo.split("+");
  let is = true;
  let keyCodeMatch;

  multi.forEach(function (v) {
    const variants = v.split("-");
    keyCodeMatch = false;

    variants.forEach(function (k2, v2) {
      if (e.keyCode === +v2) {
        keyCodeMatch = true;
      }
    });

    if (!keyCodeMatch) {
      is = false;
    }
  });

  return is;
}

export const IN_STR = 1;
export const IN_CODES = 2;

export function isDown(combo, input_type, keys_down) {
  if (!input_type) input_type = IN_STR;

  let keys;

  if (input_type === IN_STR) {
    keys = getKeyCodeCombinationFromName(combo, false);
  } else if (input_type === IN_CODES) {
    keys = combo.split("+");
  }

  for (let i = 0, len = keys.length; i < len; ++i) {
    if (!(keys_down || keysDown)[keys[i]]) {
      return false;
    }
  }

  return true;
}

export function getKeyCodeCombinationFromName(combination, do_join) {
  const names = combination.split("+");
  const key_code_combo = [];

  for (let i = 0, len = names.length; i < len; ++i) {
    const code =
      MAP[names[i].toLowerCase()] || REG_KEYS[names[i].toLowerCase()];
    key_code_combo.push(Array.isArray(code) ? code.join("-") : code);
  }

  return do_join ? key_code_combo.join("+") : key_code_combo;
}

export function getNameFromKeyCodeCombination(code_combo) {
  const codes = code_combo.split("+");
  const names = [];

  for (let i = 0, len = codes.length; i < len; ++i) {
    names.push(
      ke.capitalize(
        SPECIAL_KEYS[codes[i]] || ke.getKeyByVal(MAP, codes[i]) || ""
      )
    );
  }

  return names.join("+");
}

export function getNecessaryInfo(event) {
  return {
    keyCode: event.keyCode,
    shiftKey: event.shiftKey,
    ctrlKey: event.ctrlKey,
    altKey: event.altKey,
  };
}

export let keysDown = {};

export function clearKeysDown() {
  for (let i in keysDown) {
    delete keysDown[i];
  }
}

export function forget() {
  $(document).off("keydown");
  $(document).off("keyup");
  keysDown = {};
}

export const isModKey = isModificationKey;
export const isRegKey = isRegularKey;

export function listen(checkDeny, removeValidKey, callback, win) {
  const that = this;

  const clearLetters = function (key_code, down_keys) {
    if (isKeyCodeLetter(key_code)) {
      for (const key in down_keys) {
        if (isKeyCodeLetter(key)) {
          delete down_keys[key];
        }
      }
    }
    return down_keys;
  };

  const clearNonControlKeys = function (key_code, down_keys) {
    if (!isControlKey(key_code)) {
      for (const key in down_keys) {
        if (!isControlKey(key)) {
          delete down_keys[key];
        }
      }
    }
    return down_keys;
  };

  $(win || document).on("keydown", function (event) {
    // console.log("event.listen", event.keyCode, event.key);

    if (isValidKeyCode(event.keyCode)) {
      clearLetters(event.keyCode, keysDown);
      clearNonControlKeys(event.keyCode, keysDown);

      keysDown[checkMultipleKeyCodes(event.keyCode)] = true;

      if (event.metaKey) {
        keysDown["91-93"] = true;
      }

      checkDeny(keysDown);
      callback();
    }
  });

  $(win || document).on("keyup", function (event) {
    const kc = event.keyCode;
    const mkc = checkMultipleKeyCodes(event.keyCode);

    delete keysDown[kc];
    delete keysDown[mkc];

    removeValidKey(kc, mkc);
  });
}
