async function runOnSchedule(key, fn, interval) {
  const lastRun = await storageUtils.getVal(key) || 0;
  const now = Date.now();
  
  if (now - lastRun >= interval) {
    console.log(`Running ${key}, last run ${Math.round((now - lastRun) / 3600000)}h ago`);
    await fn();
    await storageUtils.setVal(key, now);
  } else {
    console.log(`Skipping ${key}, last run ${Math.round((now - lastRun) / 3600000)}h ago`);
  }
}

async function init() {
  ke.initSentry();
  await initStorage();
  await initFingerprinting();

  clearContextMenus();
  await initContextMenu();
  initFullpageContextMenu();

  // called before scheduled functions to ensure onboarding is shown promptly
  getProPrice().then(initIfNewUser);

  // Run scheduled functions in order
  for (const config of SCHEDULED_FUNCTIONS) {
    await runOnSchedule(config.storageKey, config.fn, config.interval);
  }  
}

export function onExtensionMessageReceived(data, sender, sendResponse) {
  if (data.action) {
    const parts = ke.parseProcessCall(data.action);

    // console.log(
    //   "action requested:",
    //   parts
    //   // processEventHandlers[parts.lib][parts.cmd][parts.exact]
    // );

    if (
      processEventHandlers[parts.lib] &&
      processEventHandlers[parts.lib][parts.cmd]
    ) {
      processEventHandlers[parts.lib][parts.cmd][parts.exact](
        data,
        function (response) {
          // TODO: this is really weird, bc the response is not always an object, but this turns everything into an object
          // for now I add a check if it's an object, but not sure it's not gonna break anything
          if (
            _.isObject(response) &&
            !_.isArray(response) &&
            typeof response !== "undefined" &&
            typeof data !== "undefined"
          ) {
            response = Object.assign({}, response, { old_data: data });
          }

          /* Prevent the following type of errors to be logged in the console:
           *
           * When a user requests something in a tab and closes it before the
           * request is managed to be handled and its response sent back.
           *
           * Thus, the response is sent to the closed tab in this case.
           */
          try {
            // console.log("response for", parts, response);
            sendResponse(response);
          } catch (e) {}
        },
        sender
      );
    } else {
      console.error(`No handler available for ${parts.lib}.${parts.cmd}`);
      sendResponse(undefined);
    }
  } else {
    sendResponse(undefined);
  }

  return true;
}

