function detectCountry() {
  return new Promise(async (resolve, reject) => {
    // don't wait for the fastest server to be picked up
    // otherwise opening onboarding page takes too long
    const country = (
      (await fetch("https://api2.matetranslate.com/geo/json").then((res) =>
        res.text()
      )) || ""
    ).toLowerCase();

    if (country && !country.includes("ip address not found")) {
      await storageUtils.setVal("user_country", country);
      await storageUtils.setIntValue("last_country_fetch_2", Date.now());
    }

    resolve();
  });
}

function getProPrice() {
  return new Promise(async (resolve, reject) => {
    if (
      Date.now() - (await storageUtils.getIntValue("last_price_fetch_2")) >=
      INTERVALS.FOUR_DAYS
    ) {
      const product_ids = [
        subscriptionConstants.ONE_OFF_ID,
        subscriptionConstants.ANNUAL_PLAN_ID,
        subscriptionConstants.MONTHLY_PLAN_ID,
      ].join(",");
      const data = await fetch(
        `https://checkout.paddle.com/api/2.0/prices?product_ids=${product_ids}&quantity=1`
      ).then((res) => res.json());

      if (data.success) {
        for (const product of data.response.products) {
          const price = product.list_price.gross + " " + product.currency;

          if (product.product_id === subscriptionConstants.ONE_OFF_ID) {
            await storageUtils.setVal("pro_inapp_price", price);
          } else if (
            product.product_id === subscriptionConstants.ANNUAL_PLAN_ID
          ) {
            await storageUtils.setVal("sub_price_annual", price);
          } else if (
            product.product_id === subscriptionConstants.MONTHLY_PLAN_ID
          ) {
            await storageUtils.setVal("sub_price_monthly", price);
          }
        }

        await storageUtils.setIntValue("last_price_fetch_2", Date.now());
      }

      resolve();
    } else {
      resolve();
    }
  });
}

const SERVERS = [
  "https://api.matetranslate.com",
  "https://api2.matetranslate.com",
  "https://asia.gikken.co/mateapi",
];

function pickFastestMateServer() {
  return new Promise(async (resolve, reject) => {
    let responses = 0;
    const response_times = new Array(SERVERS.length).fill(0);
    const p = new Ping();

    const pick = async function (ms, server_id) {
      ++responses;

      if (responses === SERVERS.length) {
        response_times[server_id] = ms;

        let fastest_server = 0;
        response_times.forEach((v, i) => {
          if (v < response_times[fastest_server]) {
            fastest_server = i;
          }
        });

        await storageUtils.setVal("misc_server", SERVERS[fastest_server]);

        resolve();
      } else {
        response_times[server_id] = ms;
      }
    };

    SERVERS.forEach((s, i) => {
      p.ping(s, function (err, ms) {
        pick(ms, i);
      });
    });
  });
}

function checkSponsorships() {
  return new Promise(async (resolve, reject) => {
    const file = await fetch(
      "https://gikken.co/files/mate/sponsorships.json"
    ).then((res) => res.json());

    if (typeof file === "object") {
      await storageUtils.encodeAndSet("sponsorships", file);
      await storageUtils.setIntValue("sponsorships_last_update", Date.now());
    }

    resolve();
  });
}

function getRemoteMateSettings() {
  return new Promise(async (resolve, reject) => {
    const file = await fetch("https://gikken.co/files/mate/settings.json");
    const options = await file.json();

    // console.log({ options });

    if (
      typeof options.should_register_fullpage_translations !== "undefined"
    ) {
      await storageUtils.setVal(
        "should_register_fullpage_translations",
        options.should_register_fullpage_translations
      );
      await storageUtils.setIntValue("settings_last_update", Date.now());
    }

    resolve();
  });
}

