let contextMenus = {};

function addContextMenuItem(combo, from, to) {
  const title =
    ke.getLocale("Kernel_Lang_" + langUtils.getLangNameByKey(from)) +
    " " +
    ke.getLocale("Kernel_Context_Into") +
    " " +
    orphographyExt.declineTransTo(to);

  const id = chrome.contextMenus.create({
    id: chrome.runtime.id + Math.random() * 1000,
    title: title,
    contexts: ["selection"],
  });

  contextMenus[id] = {
    combo: combo,
    from: from,
    to: to,
  };
}

export function clearContextMenus() {
  chrome.contextMenus.removeAll();
  contextMenus = {};
  fullpageContextMenu = null;
}

export async function initContextMenu() {
  // console.log("initContextMenu / existing:", contextMenus);

  if (Object.keys(contextMenus).length > 0) {
    clearContextMenus();
  }

  if (await storageUtils.isTrueOption("ctx_menu")) {
    const combinations = await storageUtils.getDecodedVal(
      "add_trans_combinations"
    );
    addContextMenuItem(
      "main",
      await langUtils.getFromLang(),
      await langUtils.getToLang()
    );

    for (const key in combinations) {
      if (combinations[key].from && combinations[key].to) {
        addContextMenuItem(key, combinations[key].from, combinations[key].to);
      }
    }
  }
}

export async function onContextMenuClick(info, tab) {
  // console.log({ info, tab, contextMenus, fullpageContextMenu });

  if (info.menuItemId === fullpageContextMenu) {
    await loadFullPageTranslatorOnTabX(info, tab);
    return;
  }

  const data = contextMenus[info.menuItemId];

  // console.log("onContextMenuClick / data:", data, contextMenus, info.menuItemId);

  if (!data) {
    return;
  }

  // only if pdf or local file
  let open_in_new_window = false;

  // As on 24 nov 2021, chrome v.96.0
  // Context menu called from Chrome PDF viewer (built-in extension) will drop tab object with properties:
  // url: chrome-extension://[some id]/[index.html || some other page]
  // title: [pdf original file name]
  // TODO: Check this implementation from time to time.
  const URL_parts = tab.url.split("/");
  const isPdf = info?.frameUrl?.includes(".pdf");
  // const pdf_in_title = tab.url.toLowerCase().includes(".pdf");
  if (isPdf && URL_parts[0] === "chrome-extension:") {
    open_in_new_window = true;
  }

  // local file
  if (!open_in_new_window && tab.url && tab.url.includes("about:blank")) {
    open_in_new_window = true;
  }

  if (open_in_new_window) {
    await chrome.windows.create({
      type: "panel",
      width: 337,
      height: 272,
      top: 48,
      left: 16,
      url: chrome.runtime.getURL(
        `pdf_tooltip.html#${data.from}|${data.to}|${info.selectionText}`
      ),
    });
  } else {
    await chrome.tabs.sendMessage(tab.id, {
      action: ke.processCall("app", "trans", "displayAsTooltip"),
      message: info.selectionText,
      from: data.from,
      to: data.to,
    });
  }
}

