let internal_redir_blocked = false;

// for email marketing where it opens pages like EXT_ID.com/page
async function checkInternalPagesRedirect(tabId, changedInfo, data) {
  if (internal_redir_blocked) {
    return;
  }

  if (data.status !== "complete") {
    return;
  }

  // console.log( { tabId, changedInfo, data } );

  const ext_uri = (chrome.runtime.id + ".com/").toLowerCase();

  let ff_ext_uri;
  if (ke.IS_FIREFOX) {
    ff_ext_uri = (ke.staticExtId.split("@")[0] + ".com/").toLowerCase();
  }

  // Paddle can't redirect to chrome-extension://EXT_ID/
  // It must be https://
  // Hence such a hack, it redirects to a non-existing domain where slap
  // https:// and .com around the extension ID
  // This request is caught here, all useful data taken out of it,
  // and the tab auto-closed without much visible hassle for the user
  // URL schema:
  // https://EXT_ID.com/finish_checkout?useful_data_as_query_params
  if (data.url && data.url.indexOf(ext_uri + "finish_checkout") > -1) {
    const u = new URL(data.url);
    const hash = u.searchParams.get("hash");

    // console.log("detected a checkout hash", hash);

    if (hash) {
      await finishCheckout(hash);
    }

    if (tabId) {
      try {
        await chrome.tabs.remove(tabId);
      } catch (e) {
        console.log(
          "could not remove tab after extracting a checkout hash from it:",
          e
        );
      }
    }
  } else if (
    data.url &&
    (data.url.indexOf(ext_uri) > -1 ||
      (ff_ext_uri && data.url.indexOf(ff_ext_uri) > -1))
  ) {
    // Redirect
    // {{extension_id}}.com/{{path}}
    // to
    // chrome-extension://{{path}}
    // Required for sign-in & email marketing

    // TESTS:
    // ------
    // Firefox OK
    // Chrome OK
    // Opera OK
    // Edge
    // Safari

    internal_redir_blocked = true;

    await chrome.tabs.remove(tabId);

    const redir_uri =
      ke.pathToExt +
      data.url
        .replace(ext_uri, "")
        .replace(ff_ext_uri, "")
        .replace("http://", "")
        .replace("https://", "");

    chrome.tabs.create(
      {
        url: chrome.runtime.getURL(redir_uri),
      },
      function () {
        internal_redir_blocked = false;
      }
    );
  }
}

export function checkInternalPagesRedirectSafari(
  updated_tab_id,
  changed_info,
  tab
) {
  chrome.tabs.query({}, async function (tabs) {
    for (const tab of tabs) {
      if (!tab.url) {
        return;
      }
      await checkInternalPagesRedirect(tab.id, tab);
    }
  });
}

async function finishCheckout(hash) {
  const d = await fetch(
    `https://checkout.paddle.com/api/1.0/order?checkout_id=${hash}`
  ).then((res) => res.json());

  if (d.state === "processing") {
    setTimeout(async () => {
      // console.log("transaction hasn't been processed yet — trying again in 1s");
      await finishCheckout(hash);
    }, 1000);
  } else if (d.state === "processed") {
    await saveSubscriptionData(d.order.customer.email);

    //
    // fire up the checkout callback
    chrome.tabs.query({}, function (tabs) {
      let found_settings = false;

      tabs.forEach(async function (tab) {
        if (!tab.url) {
          return;
        }

        if (tab.url.includes("options.html")) {
          await chrome.tabs.sendMessage(tab.id, {
            action: ke.processCall("app", "pro", "finish-checkout"),
            email: d.order.customer.email,
          });
          found_settings = true;
        }
      });

      // if user closed the settings page after paddle checkout got opened
      // who knows why tf someone would do that but it's still a possible case
      if (!found_settings) {
        // console.log("gotta create a new settings tab");

        chrome.tabs.create(
          {
            url: chrome.runtime.getURL("options.html"),
          },
          async (tab) => {
            // console.log("new settings tab", tab);

            setTimeout(async () => {
              await chrome.tabs.sendMessage(tab.id, {
                action: ke.processCall("app", "pro", "finish-checkout"),
                email: d.order.customer.email,
              });
            }, 500);
          }
        );
      }
    });
  }
}

