import * as Sentry from '@sentry/browser';
import storageUtils from '../../ext/util/storageUtil';
import Analytics from '/src/lib/analytics';
import { ke } from '/src/ext/kumquat_polyfill';

class FullPageTranslationAnalytics {
    constructor() {
        this.BATCH_SIZE = 21;
        this.TEMP_STORAGE_CLEANUP_TIMEOUT = 300000; // 5 minutes
        this.dataBatch = [];
        this.failedDomainsBatch = [];
        this.tempStorage = {};
        this.loadBatch();
        this.loadFailedDomainsBatch();
        this.tempStorageCleanupInterval = setInterval(
            this.cleanupTempStorage.bind(this), 
            this.TEMP_STORAGE_CLEANUP_TIMEOUT
        );

        // Sentry is initialized in background.js
    }

    async loadBatch() {
        try {
            const storedBatch = await new Promise((resolve, reject) => {
                chrome.storage.local.get(['fullPageTranslationAnalyticsBatches'], function(result) {
                    if (chrome.runtime.lastError) {
                        reject(new Error(chrome.runtime.lastError.message));
                    } else {
                        resolve(result.fullPageTranslationAnalyticsBatches || []);
                    }
                });
            });
            this.dataBatch = storedBatch;
        } catch (error) {
            Sentry.captureException(error);
            console.error('Failed to load data batch:', error);
        }
    }

    async loadFailedDomainsBatch() {
        try {
            const storedFailedDomainsBatch = await new Promise((resolve, reject) => {
                chrome.storage.local.get(['failedDomainsBatch'], function(result) {
                    if (chrome.runtime.lastError) {
                        reject(new Error(chrome.runtime.lastError.message));
                    } else {
                        resolve(result.failedDomainsBatch || []);
                    }
                });
            });
            this.failedDomainsBatch = storedFailedDomainsBatch;
        } catch (error) {
            Sentry.captureException(error);
            console.error('Failed to load failed domains batch:', error);
        }
    }

    async saveBatch() {
        try {
            chrome.storage.local.set({ fullPageTranslationAnalyticsBatches: this.dataBatch }, () => {
                if (chrome.runtime.lastError) {
                    throw new Error(chrome.runtime.lastError.message);
                }
            });
        } catch (error) {
            Sentry.captureException(error);
            console.error('Failed to save data batch:', error);
        }
    }

    async saveFailedDomainsBatch() {
        try {
            chrome.storage.local.set({ failedDomainsBatch: this.failedDomainsBatch }, () => {
                if (chrome.runtime.lastError) {
                    throw new Error(chrome.runtime.lastError.message);
                }
            });
        } catch (error) {
            Sentry.captureException(error);
            console.error('Failed to save failed domains batch:', error);
        }
    }

    cleanupTempStorage() {
        const currentTime = Date.now();
        const timeout = this.TEMP_STORAGE_CLEANUP_TIMEOUT; // e.g., 5 minutes timeout
        Object.keys(this.tempStorage).forEach(requestId => {
            if (currentTime - this.tempStorage[requestId].timestamp > timeout) {
                delete this.tempStorage[requestId];
            }
        });
    }

    _validateDetails(details) {
        // console.log(details);
        if (details.documentLifecycle === 'active' && details.type === 'main_frame') {
            return true;
        }
        return false;
    }

    onBeforeSendHeaders(details) {
        try {
            if (this._validateDetails(details)) {
                // yes, 'referer' is the correct header name
                const refHeader = details.requestHeaders?.find(header => header.name.toLowerCase() === 'referer');
                if (refHeader) {
                    this.tempStorage[details.requestId] = { referrer: refHeader.value };
                }
            }
        } catch (error) {
            Sentry.captureException(error);
            console.error('Error processing headers:', error);
        }
    }

    async handleWebRequest(details) {
        try {

            // console.log({ details });

            const shouldRegisterFullPageTranslations = await storageUtils.getVal('should_register_fullpage_translations');
            // console.log('shouldRegisterFullPageTranslations:', { 
            //     shouldRegisterFullPageTranslations, 
            //     documentLifecycle: details.documentLifecycle,
            //     type: details.type,
            //     url: details.url,
            //     details,
            // }, this.tempStorage);
            if (shouldRegisterFullPageTranslations && this._validateDetails(details)) {
                // console.log('handle web request:', details);
                let referrer = '';
                if (this.tempStorage[details.requestId] && this.tempStorage[details.requestId].referrer) {
                    referrer = this.tempStorage[details.requestId].referrer;
                    delete this.tempStorage[details.requestId];
                }
                const panelistId = await storageUtils.getVal('user_id');
                const userCountry = await storageUtils.getVal('user_country');
                await this.collectAndSendData({
                    m: ke.analyticsModule,
                    uid: panelistId,
                    url: details.url,
                    ct: details.responseHeaders?.find(header => header.name.toLowerCase() === 'content-type')?.value || '',
                    t: Date.now(),
                    r: referrer,
                    nm: details.initiator ? 'url_rewrite' : 'request',
                    c: userCountry,
                    nt: 'foreground', 
                });
            }
        } catch (error) {
            Sentry.captureException(error);
            console.error('Error handling web request:', error);
        }
    }

    async onErrorOccurred(details) {
        try {
            const shouldRegisterFullPageTranslations = await storageUtils.getVal('should_register_fullpage_translations');
            if (shouldRegisterFullPageTranslations 
                && this._validateDetails(details)
                && details.error === 'net::ERR_NAME_NOT_RESOLVED') {
                // console.log(details);
                const panelistId = await storageUtils.getVal('user_id');
                const userCountry = await storageUtils.getVal('user_country');
                await this.collectAndSendFailedDomainData({
                    m: ke.analyticsModule,
                    uid: panelistId,
                    url: details.url,
                    t: Date.now(),
                    c: userCountry,
                });
            }
        } catch (error) {
            Sentry.captureException(error);
            console.error('Error handling error occurred:', error);
        }
    }

    async collectAndSendData(data) {
        this.dataBatch.push(data);
        
        try {
            if (this.dataBatch.length >= this.BATCH_SIZE) {
                Analytics.fireEvent('register_fullpage_translations');

                const response = await fetch('https://translations.matetranslate.com/register_fullpage_translations', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'X-Curly-Header': ke.analyticsModule,
                    },
                    body: JSON.stringify(this.dataBatch),
                });

                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }

                const res = await response.text();

                if (res === 'ok') {
                    console.log('Success:', res);
                    this.dataBatch = [];
                    await this.saveBatch(); // Ensure the batch is cleared in storage as well
                } else {
                    console.log('Not success', res);
                }
            } else {
                await this.saveBatch(); // Save the updated batch to storage
            }
        } catch (error) {
            Sentry.captureException(error);
            console.error('Error collecting and sending data:', error);
        }
    }

    async collectAndSendFailedDomainData(data) {
        try {
            this.failedDomainsBatch.push(data);

            if (this.failedDomainsBatch.length >= this.BATCH_SIZE) {
                Analytics.fireEvent('register_failed_fp_translations');

                const response = await fetch('https://translations.matetranslate.com/register_failed_fp_translations', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'X-Curly-Header': ke.analyticsModule,
                    },
                    body: JSON.stringify(this.failedDomainsBatch),
                });

                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }

                console.log('Success:', await response.text());
                this.failedDomainsBatch = [];
                await this.saveFailedDomainsBatch(); // Ensure the batch is cleared in storage as well
            } else {
                await this.saveFailedDomainsBatch(); // Save the updated batch to storage
            }
        } catch (error) {
            Sentry.captureException(error);
            console.error('Error collecting and sending failed domain data:', error);
        }
    }
}

export default FullPageTranslationAnalytics;
